
import { defineComponent , ref , watch , computed } from 'vue'
import SmallModal from "@/components/SmallModal.vue"
import { useStore } from 'vuex'
import { useRouter} from 'vue-router'
import $ from 'jquery'

export default defineComponent({
    setup() {
        //router
        const router = useRouter();
        //vuex
        const store = useStore();
        //login status
        if(!localStorage.nsysua_token){
            alert("請先登入。 Please log in first.")
            router.push('/login');
        }
        //modal
        const display_modal: any = ref(false);
        //show data
        const show_data: any = ref(0);
        //language
        const lang_code:any = ref(store.state.lang_code);
        watch(lang_code, ()=>{
            show_data.value=0;
            store.commit('changeLang',lang_code.value);
        });
        //get user data
        function getUserData() {
            $.post('https://athletics.nsysu.rikujo.xyz/nsysua_backend/registration/login/user.php',{token: localStorage.nsysua_token},function(data){
                store.commit('setUserData',JSON.parse(data));
            })
        }
        getUserData();
        const user_data:any = computed(() => store.state.user_data);
        //logout
        function logout(){
            localStorage.removeItem('nsysua_token');
            router.push('/');
        }
        return {
            display_modal,
            show_data,
            lang_code,
            logout,
            getUserData,
            user_data,
        }
    },
    components: {
        SmallModal,
    }
})
