import { createStore } from 'vuex'

export default createStore({
    state: {
        lang_code: 'zh_TW',
        user_data: {},
    },
    mutations: {
        changeLang(state, langCode){
            state.lang_code = langCode;
        },
        setUserData(state,userData){
            state.user_data = userData;
        }
    },
    actions: {
    },
    modules: {
    }
})
